module.exports = "## Finger\n\n移动端手势事件识别。\n\n[→ Touch Me](https://tencent.github.io/omi/packages/omim/demos/finger/index.html)\n\n## Usage\n\n```jsx\nimport 'omim/finger'\nimport { render, h, tag, WeElement } from 'omi'\n\n@tag('my-app')\nclass MyApp extends WeElement {\n  handleTap = (evt) => {\n    this.wording += '\\r\\nTap'\n    this.update()\n  }\n\n  handleSwipe = (evt) => {\n    this.wording += '\\r\\nSwipe-' + evt.detail.direction\n    this.update()\n  }\n\n  render() {\n    return (\n      <div id='app'>\n        <m-finger onTap={this.handleTap} onSwipe={this.handleSwipe}>\n          <div class=\"touchArea\">\n            {this.wording}\n          </div>\n        </m-finger>\n      </div>\n    )\n  }\n}\n\nrender(<my-app />, 'body')\n```\n\n## API\n\n### Props\n\n```jsx\n{\n  onTouchStart: function () { },\n  TouchMove: function () { },\n  TouchEnd:  function () { },\n  TouchCancel: function () { },\n  MultipointStart: function () { },\n  MultipointEnd: function () { },\n  Tap: function () { },\n  DoubleTap: function () { },\n  LongTap: function () { },\n  SingleTap: function () { },\n  Rotate: function (evt) {\n    console.log(evt.detail.angle)\n  },\n  Pinch: function (evt) {\n    console.log(evt.detail.zoom)\n  },\n  PressMove: function (evt) {\n    console.log(evt.detail.deltaX)\n    console.log(evt.detail.deltaY)\n  },\n  Swipe: function (evt) {\n    console.log(\"swipe\" + evt.detail.direction)\n  }\n}\n```"

